      'textLong':'<span class=\'CapUp\'>Filmen om Amalia<span> <span class="CapDown">baseras p\u00e5 en sann historia om Amalia Meuller fr\u00e5n Halmstad som efter att ha f\u00e5tt tre stycken friarbrev, l\u00e4t sin b\u00e4sta v\u00e4ninna v\u00e4lja friare. Amalia gifte sig med en av friarna, Carl-Magnus Larsson. <BR><BR>Min bror producerade filmen och fr\u00e5gade mig i fall jag var intresserad av att skriva manuset och sj\u00e4lvklart ville jag det. Amalias far \u00e4r v\u00e5r stamfader p\u00e5 sl\u00e4kttr\u00e4det, s\u00e5 det k\u00e4ndes lite \'personligt ansvar\' att f\u00e5 till ett bra manus. Sj\u00e4lva h\u00e4ndelsen \u00e4r bara ett par meningar i sl\u00e4ktboken, s\u00e5 det blev en utmaning att fylla i luckorna och v\u00e4va samman en intressant historia.<BR><BR>Den st\u00f6rsta utmaningen var att f\u00e5 till dialogerna a la 1800-tal, utan att det k\u00e4ndes konstlat. Den st\u00f6rsta aha-upplevelsen var att man faktiskt pratade p\u00e5 n\u00e4stan samma s\u00e4tt d\u00e5 som nu. <BR><BR>Resultatet blev en kortfilm p\u00e5 tjugo minuter inspelad till stor del i Halmstad. Mycket tid har lagts ner p\u00e5 att hitta r\u00e4tta scenografi och kl\u00e4der. <BR><BR>Produktionstiden fr\u00e5n att jag fick uppdraget i min hand till filminspelningen b\u00f6rjade var cirka tio m\u00e5nader, s\u00e5 det var snabba ryck att f\u00e5 den f\u00e4rdig.</span>',
