      'textLong':'<span class=\'CapUp\'>\'The Devil is in the details\'<span> <span class="CapDown">brukar man ju s\u00e4ga. Att fota produkter inneb\u00e4r inte bara att ta en bild p\u00e5 en produkt - man m\u00e5ste \u00e4ven f\u00f6rst\u00e5 vad som avbildas. N\u00e4r man tar produktbilder ser man oftast hela produkten med en vit bakgrund. Detta f\u00f6r att en s\u00e5dan bild \u00e4r enkel att anv\u00e4nda till en massa olika medier. Det \u00e4r tydligt och man f\u00f6rst\u00e5r vad det \u00e4r man ser. <BR><BR>Men ibland beh\u00f6ver man ta ett par extra bilder. Oftast f\u00f6r att show detaljerna p\u00e5 produkten. Inget konstigt med det. <BR><BR>Mitt jobb blir genast lite roligare och utmanande. Att f\u00e5 g\u00e5 n\u00e4rmare in i produkten och show dess \'sj\u00e4l\'. S\u00e5dant tycker jag \u00e4r kul. Vissa g\u00e5nger f\u00e5r man verkligen v\u00e4nda och vrida p\u00e5 b\u00e5de bildvinkel och produkt innan det blir r\u00e4tt. Andra g\u00e5nger lyckas man p\u00e5 f\u00f6rsta f\u00f6rs\u00f6ket. <BR><BR>N\u00e4r man v\u00e4l \u00e4r klar och har en j\u00e4ttebra bild, har man inte bara f\u00f6rtydligat produkten, man f\u00f6rmodligen \u00e4ven h\u00f6jt v\u00e4rdet p\u00e5 produkten genom bilden.</span>',
